html,
body,
#root {
  width: 100%;
  margin: 0;
  padding: 0;
}

body,
#root {
  height: 100vh;
  overflow-y: hidden;
  background-size: cover !important;
}

.book {
  position: relative;
  float: right;
  margin-top: 3rem;
  margin-right: 8rem;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.book .page {
  height: 100%;
}

.book .page img {
  max-width: 100%;
  height: 100%;
}
